@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@700&display=swap);
html {
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #172a45;
  width: 100%;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar{position:-webkit-sticky;position:sticky;top:0;left:0;width:100%;background-color:#0a192f}.navbar .navbar-navigation{display:flex;align-items:center;justify-content:space-between}.navbar .navbar-navigation .navbar-logo{margin-left:30px;margin-top:10px;margin-bottom:10px}.navbar .navbar-navigation .navbar-logo .logo img{height:70px}.navbar .navbar-navigation .spacer{flex:1 1}.navbar .navbar-navigation .navbar-items ul{list-style:none;margin:0;padding:0;display:flex}.navbar .navbar-navigation .navbar-items li{margin:0 30px 0 30px}.navbar .navbar-navigation .navbar-items li:hover{background-color:black;cursor:pointer}.navbar .navbar-navigation .navbar-items li .link{color:white;text-decoration:none}@media (max-width: 768px){.navbar-items{display:none}}

.toggle-btn{display:flex;flex-direction:column;justify-content:space-around;height:25px;width:30px;background:transparent;border:none;cursor:pointer;padding:0}.toggle-btn:focus{outline:none}.toggle-btn .toggle-btn-line{width:30px;height:2px;background-color:white}

footer{background-color:#0a192f}footer .footer{padding-top:20px;padding-bottom:20px;text-align:center}

.side-menu{background-color:#1d2323;height:100%;position:fixed;top:0;left:0;width:70%;max-width:400px;z-index:200;-webkit-transform:translateX(-100%);transform:translateX(-100%);transition:-webkit-transform 0.5s;transition:transform 0.5s;transition:transform 0.5s, -webkit-transform 0.5s}.side-menu ul{height:100%;list-style:none;display:flex;flex-direction:column;justify-content:center;margin:0}.side-menu ul li{margin:20px 0}.side-menu ul li .link{text-decoration:none;color:white}.side-menu ul li .link:hover{cursor:pointer;border-bottom:1px solid white}.side-menu.open{-webkit-transform:translateX(0);transform:translateX(0)}@media (max-width: 768px){.toogle-btn{margin:10px 0}}@media (min-width: 769px){.toogle-btn{display:none}}

.backdrop{position:fixed;width:100%;height:100%;top:0;left:0;background-color:rgba(0,0,0,0.3);z-index:100}

.box-container{display:flex;justify-content:space-around}

.box{background-color:#0a192f;border-radius:10px;width:220px;height:120px;display:flex;justify-content:center;align-items:center;border:2px solid #303c55;font-family:"Roboto Mono", monospace;font-size:20px;text-decoration:none;color:white}.box:hover{cursor:pointer}


.homepage{height:100%;margin-top:30px}.homepage h1{font-family:"Roboto Mono", monospace}

.App {
  color: white;
  box-sizing: border-box;
  height: 100vh;
}

.container {
  padding: 0 60px;
}

